body.active-modal {
    overflow-y: hidden;
}

.btn-modal {
    padding: 10px 20px;
    display: block;
    margin: 100px auto 0;
    font-size: 18px;
}

.modal, .overlay {
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
}

.overlay {
    background: rgba(49,49,49,0.8);
}
.modal-content {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    /* line-height: 1.4; */
    background: #f1f1f1;
    /* padding: 14px 28px; */
    border-radius: 3px;
    max-width: 600px;
    min-width: 300px;
}



.modal {
    display: flex;
    justify-content: center;
    align-items: center;
    /* Add other styles as needed */
}

.modal-content {
    background: white;
    padding: 20px;
    border-radius: 5px;
    height: 85vh;
    width: 70%; /* Set your desired width here */
    max-width: 90%;
    margin-top: 80px; /* Added margin to move the div down */
    
    /* Add other styles as needed */
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    /* Add other styles as needed */
}

.header-content{
    /* background-color:red; */
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;


    .close-modal {
        background-color: transparent;
        border-color: #f1f1f1;
        height: 40px;
        width: 40px;
    }
}

.body-content{
    

    .input-fields{
        display: grid;
    grid-template-columns: auto auto;
    gap: 20px;
    }
    .fields{
        display: flex;
        flex-direction: column;

        input{
            height: 40px;
            border-radius: 10px;
            /* margin-bottom: 0px;
            margin-top: 0; */
        }
        select{
            height: 40px;
            border-radius: 10px;
        }
    }

    .checkBox{
        display: flex;
        flex-direction: row;
        gap: 20px;
        .fields{
            display: flex;
            flex-direction: row;
            gap: 1rem;
        }
    }
}