
/* CSS */
body {
  margin: 0;
  font-family: Arial, sans-serif;
  overflow: hidden; /* Prevent scrolling */
}

.login-container {
  display: flex;
  height: 100vh;
}

.login-image-section {
  flex: 1;
  position: relative;
}

.login-background-image {
  width: 100%;
  height: 100%;
  border: none;
  object-fit: cover;
}

.login-overlay {
  position: absolute;
  top: 5%;
  left: 15%;
  text-align: center;
  color: white;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
}

.login-overlay h1 {
  font-size: 4rem;
  /* font-family: "Satisfy", serif;
  font-weight: 400;
  font-style: normal; */
  font-family: "Dancing Script", serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;

}

.login-overlay p {
  font-size: 1.3rem;
  width: 30rem;
  position: relative;
  top: -40px;
  font-family: "Dosis", serif;
  font-optical-sizing: auto;
  font-weight: <weight>;
  font-style: normal;
}


/* Right Side Section */
.login-right-section {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  box-sizing: border-box;
}

.login-box {
  width: 100%;
  max-width: 400px;
  background: white;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  /* box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1); */
  border-radius: 8px;
}

.login-box h1 {
  font-size: 4.3rem;
  margin-bottom: -12px;
  color: var(--primary-color);
}



.login-box p {
  font-size: 0.9rem;
  color: rgba(0, 0, 0, 0.6);
}

.login-input-group {
  margin-bottom: 15px;
  text-align: left;
}

.login-input-group label {
  font-size: 14px;
  position: relative;
  width: 4rem;
  margin-bottom: 5px;
  top: 15px;
  left: 20px;
  background-color: white;
  color: var(--primary-color);
  display: block;
}

.login-input-group input {
  width: 100%;
  padding: 15px;
  border: 1px solid var(--primary-color);
  border-radius: 8px;
  font-size: 14px;
  box-sizing: border-box;
}

.login-button {
  width: 50%;
  padding: 12px;
  background-color: var(--primary-color);
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  margin-top: 15px;
}

.login-button:hover {
  background-color: black;
}
