/*  */

.login-forgot-password {
  text-align: right;
  font-size: 0.9rem;
  color: #007bff;
  cursor: pointer;
  margin-top: -10px;
  margin-bottom: 15px;
}

.login-social-login p {
  margin: 15px 0;
  font-size: 0.9rem;
  color: #888;
}

.login-social-buttons button {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 5px;
  padding: 10px 15px;
  position: relative;
  left: 80px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background-color: #f5f5f5;
  color: #555;
  font-size: 1rem;
  gap: 8px;
  text-align: center;
}

.login-social-buttons button .google-icon-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.login-social-buttons button .google-icon {
  font-size: 1.5rem;
}

.login-social-buttons button .google-text {
  display: inline-block;
  margin-left: 8px;
}


.login-register-link {
  font-size: 0.9rem;
  color: #555;
}

.login-register-link a {
  color: var(--primary-color);
  text-decoration: none;
}

.login-footer-design {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 10px;
}

