body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  .slider {
    position: relative;
  }
  
  .slider-image {
    position: relative;
  }
  .slider-image::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: #324764;
    opacity: 0.6; /* Adjust the opacity as needed */
    /* z-index: 1;  */
  }
  
  .slider-content {
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -30%);
    color: white;
    text-align: center;
    text-shadow: 0px 2px 10px rgba(0, 0, 0, 0.5);
  }
  
  
  .btn-green {
    padding: 5px 10px;
    background-color: var(--primary-color);
    border: none;
    color: white;
    border-radius: 5px;
    margin-left: 1rem;
    cursor: pointer;
    font-size: 16px;
  }
  
  .slider-arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    border: none;
    font-size: 24px;
    cursor: pointer;
    width: 40px;
    height: 40px;
    border-radius: 50%;
  }
  
  .slider-arrow.left {
    left: 10px;
  }
  
  .slider-arrow.right {
    right: 10px;
  }
  
  /* Search Section */
  .search-section {
    margin-top: 20px;
    bottom: -60px;
    max-width: 1500px;
    margin-left: auto;
    margin-right: auto;
    transform: translateY(-70%);
    width: 90%;
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    border-radius: 8px;
  }
  
  .search-container {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    justify-content: center;
  }
  
  .search-field {
    display: flex;
    flex-direction: column;
    margin-right: 10px;
  }
  
  .search-field label {
    margin-bottom: 5px;
    font-weight: bold;
  }
  
  .search-field input,
  .search-field select {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    width: 200px;
  }
  
  .btn {
    margin-top: 15px;
  }
  .uil-search{
    font-size: 2rem;
    margin: 1rem;
  }
  


  /* card silder css */

  .working-holiday-container {
    display: flex;
    flex-wrap: wrap; /* Ensure responsiveness */
    align-items: flex-start; /* Adjust alignment for varied content heights */
    gap: 20px; /* Add space between sections */
    padding: 20px;
  }

  .premium{
    background: linear-gradient(135deg, rgba(255, 183, 183, 0.3), rgba(255, 255, 255, 0.3), rgba(255, 255, 183, 0.3));
  }
  
  .green-block {
    flex: 1 1 30%; /* Ensure flexibility on smaller screens */
    max-width: 300px; /* Prevent overly large blocks */
    background-color: transparent;
    color: white;
    margin-left: 5rem;
    padding: 20px;
    border-radius: 10px;
  }
  
  .green-block h2 {
    font-size: 2.5rem;
    color: var(--primary-color);
    font-family: "Dancing Script", serif;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
    margin-bottom: 2px;
  }

  .green-block p {
    color: black;
    margin-top: 0;
    line-height: 2;

    font-size: 0.8rem;
   }

  .green_block-pra{
    margin-left: 5px;
    color: #000;
    margin: 0;
    margin-bottom: 1rem;    
    font-size: 1.5rem;
    text-transform: uppercase;
    font-weight: 800;
  }
  .uil-arrow-right{
    font-size: 2rem;
    position: relative;
    padding: 5px 10px;
    top:8px;
    color: white;
    background-color: var(--primary-color);
    border-radius: 60%;
    margin-right: 1rem;
  }
  
  
  .green-block button {
    padding: 5px 10px;
    background-color: transparent;
    border: none;
    font-size: 0.8rem;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .card-slider {
    flex: 1 1 50%;
    position: relative;
    display: flex;
    overflow: hidden;
    max-width: 100%;
  }
  
  .cards {
    display: flex;
    transition: transform 0.5s ease-in-out;
    /* gap: 20px; Space between cards */

  }
  
  .card {  
    position: relative;
    display: flex;
    align-items: flex-end;
    overflow: hidden;
    margin-right: 2rem;
    padding: 1rem;
    width: 220px; /* Match your existing card width */
    text-align: center;
    color: whitesmoke;
    background-color: whitesmoke;
    border-radius: 10px;
    box-shadow: 0 1px 1px rgba(0,0,0,0.1), 
      0 2px 2px rgba(0,0,0,0.1), 
      0 4px 4px rgba(0,0,0,0.1), 
      0 8px 8px rgba(0,0,0,0.1),
      0 16px 16px rgba(0,0,0,0.1);
    transition: transform 0.7s cubic-bezier(0.19, 1, 0.22, 1), box-shadow 0.7s cubic-bezier(0.19, 1, 0.22, 1);
  }

  .card:hover {
    transform: scale(1.05); /* Slightly enlarge on hover */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2), 
      0 8px 12px rgba(0, 0, 0, 0.2), 
      0 16px 24px rgba(0, 0, 0, 0.2);
  }
  
  .card .content {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; /* Center all content vertically */
    text-align: center; /* Align text in the center horizontally */
    width: 100%;
    padding: 1rem;
    transform: translateY(calc(100% - 4rem));
    transition: transform 0.7s cubic-bezier(0.19, 1, 0.22, 1);
    z-index: 1;
}


  .card-image {
    height:30px;
    background-size: cover;
    background-position: center;
  }
  .card:hover .content {
    transform: translateY(0);

  }
  
  .card .content h3 {
    font-size: 1.2rem;
    font-weight: bold;
    color: black;
  }
  
  .card .content p {
    font-size: 0.8rem;
    color: #555;
    margin-left: 2rem;
    margin: 0.5rem 1;
  }
  
  .card .content button {
    margin-top: 1rem;
    padding: 0.5rem 1rem;
    font-size: 0.9rem;
    font-weight: bold;
    text-transform: uppercase;
    color: white;
    background-color: black;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .card .content button:hover {
    background-color: darkgray;
  }
  
  
  .slider-button {
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    border: none;
    padding: 10px 15px;
    border-radius: 50%;
    cursor: pointer;
     z-index: 2;
  }

.slider-button:hover {
  background-color: rgba(0, 0, 0, 0.7);
}

/* Ensure only the last card aligns properly */
.cards:last-child {
  margin-right: 0; /* No margin for the last card */
}
  