/* button +*/
.button-container {
    display: flex;
    justify-content: center;
    padding: 0.75rem 1rem;
  }
  
  .create-trip-button {
    display: flex;
    min-width: 84px;
    max-width: 480px;
    align-items: center;
    justify-content: center;
    height: 3rem;
    padding: 0 1.25rem;
    background-color: blueviolet;
    color: #ffffff;
    border: none;
    gap: 0.5rem;
    border-radius: 9999px;
    cursor: pointer;
    font-size: 1rem;
    margin-left: 50%;
    font-weight: bold;
    line-height: normal;
    letter-spacing: 0.015em;
    overflow: hidden;
  }
  
  .create-trip-button .icon {
    color: #ffffff;
  }
  
  .create-trip-button .button-text {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }
  
/* button end  */

/* card  feacture csss */
.text-title {
    color: #1C160C;
    font-size: 2rem;
    font-weight: bold;
    line-height: 1.3;
    letter-spacing: -0.015em;
    margin-left: 4rem;
    padding: 20px 16px 12px;
  }
  
  .grid-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(158px, 1fr));
    gap: 12px;
    padding: 16px;
    margin: 0 5rem;
  }
  
  .card {
    display: flex;
    flex: 1;
    gap: 12px;
    border-radius: 8px;
    border: 1px solid none;
    background-color: #e6e6fa;
    padding: 16px;
    flex-direction: column;
  }
  
  .card-icon {
    color: #1C160C;
  }
  
  .card-title {
    color: #1C160C;
    font-size: 16px;
    font-weight: bold;
    line-height: 1.3;
  }
  
  .card-description {
    color: blueviolet;
    font-size: 14px;
    font-weight: normal;
    line-height: 1.5;
  }
  

  /* Popular Destinations card  */

  .popular-destinations-title {
    color: #1c160c;
    font-size: 2rem;
    font-weight: bold;
    margin-left: 4rem;
    line-height: 1.2;
    letter-spacing: -0.015em;
    padding: 1.25rem 1rem 0.75rem 1rem;
  }
  
  .popular-destinations-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(158px, 1fr));
    gap: 0.75rem;
    padding: 1rem;
    margin: 0 5rem;
  }
  
  .destination-card {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
    padding-bottom: 0.75rem;
  }
  
  .destination-image {
    width: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    aspect-ratio: 16 / 9;
    border-radius: 0.75rem;
  }
  
  .destination-name {
    color: #1c160c;
    font-size: 1rem;
    font-weight: 500;
    line-height: normal;
  }
  
  .destination-description {
    color: blueviolet;
    font-size: 0.875rem;
    font-weight: 400;
    line-height: normal;
  }
  