/* General Container Styling */
.slider-container {
    width: 65%;
    max-width: 1200px;
    margin: 0 auto;
    margin-top: 0.5rem;
    padding: 20px;
    box-sizing: border-box;
}

/* Flexbox for Four Boxes */
.slide-boxes {
    display: grid;
    grid-template-columns: repeat(2, 1fr);   
    justify-items: center;
    gap: 1rem;
    align-items: start; /* Ensures alignment across all boxes */
}

/* Text Box Styling */
.text-box {
    background-color: #e6e6fa; /* Lavender background */
    width: 100%; /* Ensures it stays within grid boundaries */
    height: 300px; /* Set a fixed height */
    min-height: 300px;
    padding: 20px  ;
    box-sizing: border-box;
    position: relative;
    bottom: -70px;
    right: -40px;
    border-radius: 2px;
    overflow: visible;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1);
}

.text-box h2 {
    margin-bottom: 10px;
    font-size: 1.5rem;
    color: #333;
    line-height: 1.2em;
}

.text-box h3 {
    margin-bottom: 10px;
    font-size: 1rem;
    color: #b76ef8;
    line-height: 1.2em;
}

.text-box p {
    font-size: 0.9rem;
    color: #666;
    line-height: 1.4em;
    max-height: 100px; /* Limit paragraph height */
    overflow: hidden; /* Prevents overflow */
    text-overflow: ellipsis; /* Adds ellipsis for truncated text */
}

/* Button Styling */
.button {
    border: 1px solid black;
    background-color: transparent;
    font-size: 1.1rem;
    padding: 0.7rem;
    text-transform: uppercase;
    margin-top: 1rem;
    cursor: pointer;
    border-radius: 2px;

}

.button:hover {
    background-color: black;
    color: white;
}

.uil-arrow-right {
    color: #b76ef8;
    font-size: 1.5rem;
}

/* Image Box Styling */
.image-box {
    width: 100%; /* Ensures consistent size within grid */
    height: 300px; /* Fixed height to match text box */
    min-height: 300px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.image-box img {
    width: 100%;
    height: 100%;
    border-radius: 3px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    object-fit: cover;
}
