.slider-control {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background-color: white;
    color: blueviolet;
    border: none;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    font-size: 40px;
    cursor: pointer;
    z-index: 1000;
  }
  
  .slider-control.left {
    left: 180px;
  }
  
  .slider-control.right {
    right: 180px;
