body {
    margin: 0;
    font-family: Arial, sans-serif;
    overflow: hidden; /* Prevent scrolling */
  }
  
  .register-container {
    display: flex;
    height: 100vh;
  }
  
  .register-image-section {
    flex: 1;
    position: relative;
  }
  
  .register-background-image {
    width: 100%;
    height: 100%;
    border: none;
    object-fit: cover;
  }
  
  .register-overlay {
    position: absolute;
    top: 5%;
    left: 15%;
    text-align: center;
    color: white;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
  }
  
  .register-overlay h1 {
    font-size: 4rem;
    font-family: "Dancing Script", serif;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
  }
  
  .register-overlay p {
    font-size: 1.3rem;
    width: 30rem;
    position: relative;
    top: -40px;
    font-family: "Dosis", serif;
  }
  
  /* Right Side Section */
  .register-right-section {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    box-sizing: border-box;
  }
  
  .register-box {
    width: 100%;
    max-width: 400px;
    background: white;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    border-radius: 8px;
  }
  
  .register-box h1 {
    font-size: 4.3rem;
    margin-bottom: -12px;
    color: var(--primary-color);
  }
  
  .register-box p {
    font-size: 0.9rem;
    color: rgba(0, 0, 0, 0.6);
  }
  
  .register-input-group {
    margin-bottom: 1px;
    text-align: left;
  }
  
  .register-input-group label {
    font-size: 14px;
    position: relative;
    width: 4.2rem;
    margin-bottom: 5px;
    top: 15px;
    left: 20px;
    background-color: white;
    color: var(--primary-color);
    display: block;
  }
  
  .register-input-group input {
    width: 100%;
    padding: 15px;
    border: 1px solid var(--primary-color);
    border-radius: 8px;
    font-size: 14px;
    box-sizing: border-box;
  }
  
  .register-button {
    width: 50%;
    padding: 12px;
    background-color: var(--primary-color);
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    margin-top: 15px;
  }
  
  .register-button:hover {
    background-color: black;
  }
  
  .register-forgot-password {
    text-align: right;
    font-size: 0.9rem;
    color: #007bff;
    cursor: pointer;
    margin-top: -10px;
    margin-bottom: 15px;
  }
  
  .register-social-login p {
    margin: 15px 0;
    font-size: 0.9rem;
    color: #888;
  }
  
  .register-social-buttons button {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 5px;
    padding: 10px 15px;
    position: relative;
    left: 80px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background-color: #f5f5f5;
    color: #555;
    font-size: 1rem;
    gap: 8px;
    text-align: center;
  }
  
  .register-social-buttons button .google-icon-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .register-social-buttons button .google-icon {
    font-size: 1.5rem;
  }
  
  .register-social-buttons button .google-text {
    display: inline-block;
    margin-left: 8px;
  }
  
  .register-login-link {
    font-size: 0.9rem;
    color: #555;
    cursor: pointer;
  }
  
  .register-login-link a {
    color: var(--primary-color);
    text-decoration: none;
  }
  
  .register-footer-design {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    gap: 10px;
  }
  
  .register-taj-mahal {
